import { createAction, createReducer } from 'redux-act';

const initialState = {
  auth: false,
  accessToken: null,
  user: {},
  lang: 'en',
};

export const fetchAuth = createAction('fetch auth', ({ auth, accessToken }) => ({
  auth,
  accessToken,
}));
export const fetchUser = createAction('fetch user', user => ({ user }));

export const changeLang = createAction('change language', lang => ({ lang }));

export default createReducer(
  {
    [fetchAuth]: (state, { auth, accessToken }) =>
      Object.assign({}, state, {
        auth,
        accessToken,
      }),
    [fetchUser]: (state, { user }) => Object.assign({}, state, { user }),
    [changeLang]: (state, { lang }) => Object.assign({}, state, { lang }),
  },
  initialState
);
